import {
  getGroups,
  getDepartments,
  getStudents,
  getTeachers
} from "./../state/selectors/app.selectors";
import { State } from "./../state/state";
import { Store } from "@ngrx/store";
import { ChangeDetectionStrategy, Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { getSelected } from "../state/selectors/misses.selectors";
import { Router } from "@angular/router";
import { dispatchLoadAction } from "../utils/utils";

@Component({
  selector: "app-misses-actions",
  templateUrl: "./misses-actions.component.html",
  styleUrls: ["./misses-actions.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MissesActionsComponent {
  options: string[] = ["One", "Two", "Three"];
  filters: FormGroup;
  selected$ = this.store.select(getSelected);

  dispatchLoadAction = dispatchLoadAction(this.store);
  groups$ = this.store.select(getGroups);
  departments$ = this.store.select(getDepartments);
  students$ = this.store.select(getStudents);
  teachers$ = this.store.select(getTeachers);

  constructor(
    private formBuilder: FormBuilder,
    private store: Store<State>,
    private router: Router
  ) {
    this.filters = this.formBuilder.group({
      department: [""],
      teacher: [""],
      student: [{ value: "", disabled: true }],
      group: [""],
      dateFrom: [""],
      dateTo: [""]
    });
  }

  navigate(url: string) {
    this.router.navigateByUrl(url);
  }
  resetFilters() {
    this.filters.reset();
  }
}
