import { AddMissesService } from "./../../add-misses/add-misses.service";
import { Injectable } from "@angular/core";
import { Actions, Effect, ofType } from "@ngrx/effects";
import { of } from "rxjs";
import { catchError, map, switchMap, delay } from "rxjs/operators";
import { MissesTableService } from "src/app/misses-table/misses-table.service";
import * as missesActions from "../actions/misses.actions";

@Injectable()
export class MissesEffects {
  constructor(
    private actions$: Actions,
    private missesTableService: MissesTableService,
    private addMissesService: AddMissesService
  ) {}

  @Effect()
  loadMisses$ = this.actions$.pipe(
    ofType(missesActions.MissesActionTypes.LOAD),
    delay(2000),
    switchMap(() =>
      this.missesTableService.getMisses().pipe(
        map(misses => new missesActions.LoadSuccess(misses)),
        catchError(err => of(new missesActions.LoadFail(err)))
      )
    )
  );

  @Effect()
  addMisses$ = this.actions$.pipe(
    ofType(missesActions.MissesActionTypes.ADD),
    switchMap(misses =>
      this.addMissesService.addMisses(misses).pipe(
        map(() => new missesActions.AddSuccess()),
        catchError(err => of(new missesActions.AddFail(err)))
      )
    )
  );
}
