import {
  ChangeDetectionStrategy,
  Component,
  OnDestroy,
  OnInit
} from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import {
  MatSnackBar,
  MatSnackBarConfig,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition
} from "@angular/material";
import { Store } from "@ngrx/store";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { searchSelectValidator } from "../search-select/search-select.validator";
import * as missesActions from "../state/actions/misses.actions";
import {
  getDepartments,
  getGroups,
  getStudents,
  getSubjects,
  getTeachers
} from "../state/selectors/app.selectors";
import { dispatchLoadAction } from "../utils/utils";
import { Teacher } from "./../models/teacher.model";
import { State } from "./../state/state";

@Component({
  selector: "app-add-misses",
  templateUrl: "./add-misses.component.html",
  styleUrls: ["./add-misses.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AddMissesComponent implements OnInit, OnDestroy {
  foods = [
    { value: "steak-0", viewValue: "Steak" },
    { value: "pizza-1", viewValue: "Pizza" },
    { value: "tacos-2", viewValue: "Tacos" }
  ];
  snackBarText = "Отработка успешно добавлена";
  snackBarActionText = "Скрыть";
  duration: number = 2000;
  horizontalPosition: MatSnackBarHorizontalPosition = "center";
  verticalPosition: MatSnackBarVerticalPosition = "bottom";

  dispatchLoadAction = dispatchLoadAction(this.store);
  groups$ = this.store.select(getGroups);
  departments$ = this.store.select(getDepartments);
  students$ = this.store.select(getStudents);
  teachers$ = this.store.select(getTeachers);
  subjects$ = this.store.select(getSubjects);

  addMissesForm: FormGroup;
  destroy$ = new Subject<boolean>();
  constructor(
    private formBuilder: FormBuilder,
    private store: Store<State>,
    private matSnackBar: MatSnackBar
  ) {}

  ngOnInit() {
    this.addMissesForm = this.formBuilder.group({
      department: ["", Validators.required],
      group: ["", [Validators.required, searchSelectValidator]],
      student: [{ value: "", disabled: true }, Validators.required],
      subject: ["", Validators.required],
      missDate: ["", Validators.required],
      hours: [
        "",
        [Validators.required, Validators.pattern("^[0-9]*[.]?[0-9]+$")]
      ],
      type: ["", Validators.required],
      teacher: ["", Validators.required],
      rank: [{ value: "", disabled: true }, Validators.required],
      hourCost: [
        "",
        [Validators.required, Validators.pattern("^[0-9]*[.]?[0-9]+$")]
      ]
    });

    this.addMissesForm
      .get("teacher")
      .valueChanges.pipe(takeUntil(this.destroy$))
      .subscribe((data: Teacher) => {
        this.addMissesForm.get("rank").patchValue(data.teacher_rank);
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  addMisses() {
    this.store.dispatch(new missesActions.Add(this.addMissesForm.value));
  }

  openSnackBar() {
    const config = new MatSnackBarConfig();
    config.verticalPosition = this.verticalPosition;
    config.horizontalPosition = this.horizontalPosition;
    config.duration = this.duration;
    this.matSnackBar.open(this.snackBarText, this.snackBarActionText, config);
  }
}
