import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  ViewChild,
  OnInit
} from "@angular/core";
import {
  ControlValueAccessor,
  FormControl,
  NG_VALUE_ACCESSOR,
  Validators
} from "@angular/forms";
import { MatSelect, PageEvent } from "@angular/material";
import { Observable } from "rxjs";

@Component({
  selector: "app-search-select",
  templateUrl: "./search-select.component.html",
  styleUrls: ["./search-select.component.scss"],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SearchSelectComponent),
      multi: true
    },
    {
      provide: MatSelect
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SearchSelectComponent implements OnInit, ControlValueAccessor {
  select = new FormControl("");
  filter = new FormControl("");
  searchSelect = new FormControl("");
  isDisabled = false;

  @Input("dataStream") dataStream$: Observable<any>;
  @Input("placeholder") placeholder: string;
  @Input("required") isRequired: boolean;
  @Input("dependency") dependency: FormControl;
  @ViewChild("search") searchField: ElementRef;

  currentPageIndex = 0;
  pageSize = 5;
  pageSizeOptions: number[] = [5];

  constructor(private cdr: ChangeDetectorRef) {}

  ngOnInit(): void {
    if (this.isRequired) {
      this.select.setValidators(Validators.required);
    }
  }

  onChange: any = () => {};
  onTouched: any = () => {};

  setFocus() {
    this.searchField.nativeElement.focus();
  }

  optionSelect(data) {
    if (this.dependency) {
      if (data instanceof Object) {
        this.dependency.enable();
      } else {
        this.dependency.disable();
      }
    }
    this.writeValue(data);
    this.onTouched();
    this.filter.setValue("");
  }

  onPaginateChange(event: PageEvent) {
    this.currentPageIndex = event.pageIndex * this.pageSize;
  }

  writeValue(obj: any): void {
    if (obj === null) {
      this.select.reset();
    }
    this.onChange(obj);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState(isDisabled: boolean): void {
    if (isDisabled) {
      this.select.disable();
    } else {
      this.select.enable();
    }

    this.cdr.detectChanges();
  }
}
