import { AddMissesComponent } from './../add-misses/add-misses.component';
import { PrintMissesComponent } from './../print-misses/print-misses.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MissesComponent } from '../misses/misses.component';

const routes: Routes = [
  {path: 'misses', component: MissesComponent},
  {path: 'printmisses', component: PrintMissesComponent},
  {path: 'addmisses', component: AddMissesComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class MainNavRoutingModule { }
