import { SelectionModel } from "@angular/cdk/collections";
import {
  ChangeDetectionStrategy,
  Component,
  OnDestroy,
  OnInit,
  ViewChild
} from "@angular/core";
import { MatTableDataSource, MatPaginator } from "@angular/material";
import { Store } from "@ngrx/store";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import * as missesActions from "../state/actions/misses.actions";
import { Misses } from "./../models/misses.model";
import { getMisses } from "./../state/selectors/misses.selectors";
import { State } from "./../state/state";

@Component({
  selector: "app-misses-table",
  templateUrl: "./misses-table.component.html",
  styleUrls: ["./misses-table.component.css"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MissesTableComponent implements OnInit, OnDestroy {
  misses$ = this.store.select(getMisses);
  private readonly destroy$ = new Subject<boolean>();

  displayedColumns: string[] = [
    "select",
    "department",
    "year",
    "group",
    "course",
    "student",
    "subject",
    "miss_type",
    "date",
    "quantity_hours",
    "hour_cost",
    "teacher",
    "cause",
    "paided",
    "worked",
    "foreigner"
  ];

  @ViewChild(MatPaginator) matPaginator: MatPaginator;
  dataSource = new MatTableDataSource<Misses>();
  selection = new SelectionModel<Misses>(true, []);

  constructor(private store: Store<State>) {}

  ngOnInit() {
    this.dataSource.paginator = this.matPaginator;
    this.store.dispatch(new missesActions.Load());

    this.misses$
      .pipe(takeUntil(this.destroy$))
      .subscribe(data => (this.dataSource.data = data));

    this.store.dispatch(new missesActions.ClearSelect());
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  masterToggle() {
    this.isAllSelected()
      ? this.selection.clear()
      : this.dataSource.data.forEach(row => this.selection.select(row));
  }

  updateSelect() {
    this.store.dispatch(
      new missesActions.UpdateSelect(this.selection.selected)
    );
  }
}
